import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { JobService } from 'src/app/service/job.service';
import { CandidateService } from 'src/app/service/candidate.service';
import { FindCV } from 'src/app/models/findCV';

@Component({
  selector: 'app-detail-interview',
  templateUrl: './detail-interview.component.html',
  styleUrls: ['./detail-interview.component.scss']
})
export class DetailInterviewComponent implements OnInit {
  idJob:any;
  id:any;
  detailJobInterView:any[]=[];
  confirm:FindCV;
  constructor(private candidateService:CandidateService,private jobService: JobService,private router: Router,private _Activatedroute:ActivatedRoute) { }
  findCv:FindCV;
  ngOnInit() {
    this.confirm=new FindCV();
    this.findCv=new FindCV();
    //xac dnh ngay day
    this.id=this._Activatedroute.snapshot.params['id'];
    this.idJob=this._Activatedroute.snapshot.params['idJob'];
    this.findCv.idJob=this.idJob;
    this.findCv.idUser=this.id;
    this.jobService.getDetailInterview(this.findCv)
    .subscribe(res =>{
      this.detailJobInterView=res;
      console.log(this.detailJobInterView);
  });
  }
  Yes()
  {
    // alert('xx');
    this.confirm.idJob=this.idJob;
    this.confirm.idUser=this.id;
    this.confirm.confirm=true;
    this.jobService.ConfirmRequest(this.confirm)
    .subscribe(res =>{
      alert('Xác nhận thành công!');
  });
  }
  No()
  {
    this.confirm.idJob=this.idJob;
    this.confirm.idUser=this.id;
    this.confirm.confirm=false;

    this.jobService.ConfirmRequest(this.confirm)
    .subscribe(res =>{
    });
  }

}
