import { Component, OnInit } from '@angular/core';
import { CandidateService } from 'src/app/service/candidate.service';
import { JobService } from 'src/app/service/job.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-view-cv',
  templateUrl: './view-cv.component.html',
  styleUrls: ['./view-cv.component.scss']
})
export class ViewCVComponent implements OnInit {
  img:string;
  check:boolean=true;
  danhGia:any="";
  nhanXet:any="";
  constructor(private _sanitizer: DomSanitizer,private candidateService:CandidateService,private jobService: JobService) {
    
    
    this.jobService.getInfoCandidate(this.candidateService.getId())
    .subscribe(res =>{
        console.log(res[0].xacNhan);
        this.check=res[0].xacNhan;
        this.danhGia=res[0].danhGia;
        this.nhanXet=res[0].nhanXet;
        console.log(res);
    });


    this.jobService.getCV(this.candidateService.getId())
      .subscribe(res =>{
         this.img=res;
        //  alert(res);
        //     this.img = this._sanitizer.bypassSecurityTrustResourceUrl('data:image/jpg;base64,' 
        //           + res);
       // alert(this.img);
      });

      
    }
    


  ngOnInit() {
  }
  Request()
  {

    this.jobService.getRequest(this.candidateService.getId())
    .subscribe(res =>{
      alert('Yêu cầu xác nhận CV thành công!');
    });

  }

}
