import { Component, OnInit } from '@angular/core';
import { JobService } from 'src/app/service/job.service';
import { CandidateService } from 'src/app/service/candidate.service';

@Component({
  selector: 'app-list-job-interview',
  templateUrl: './list-job-interview.component.html',
  styleUrls: ['./list-job-interview.component.scss']
})
export class ListJobInterviewComponent implements OnInit {
  listJobInterview:any[]=[];
  colsCandidate:any[];
  
  constructor(private jobService: JobService,private candidateService:CandidateService) { }

  ngOnInit() {

    this.colsCandidate=[
      { field: 'id', header: 'idJob' },
      { field: 'level', header: 'Vị trí làm việc' },
      { field: 'title', header: 'Tên công việc' },
      { field: 'dateApply', header: 'Ngày nộp' },
   ]

    alert('taisao'+this.candidateService.getId());
    this.jobService.getJobInterview(this.candidateService.getId())
    .subscribe(res =>{
      this.listJobInterview=res;

      console.log(this.listJobInterview);
  });
}

}
